
* {
  box-sizing: border-box;
}
#facebook-post {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}
#overlay {
  content: '';
  position: absolute;
  background: #000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#download {
  overflow: hidden;
  position: relative;
}
.text {
  display: block;
  width: 80%;
  position: absolute;
}



